import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";


export class FBDatePickerOptions extends FBFieldWithPriceBaseOptions{
    public DefaultDate:string;
    public Placeholder:string;

    public ShowQuantitySelector:boolean;
    public Format:string;
    public WeekStartOn: number;



    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.Type=FieldTypeEnum.Datepicker;
        this.Label='Datepicker';
        this.DefaultDate='';
        this.Placeholder='';
        this.Format='dd/MM/yyyy';
        this.WeekStartOn=0;

    }
}
